let defaultState = {
  form: {},
  submitting: false,
};

export default function landingReducer(state = defaultState, action) {
  switch (action.type) {
    case 'UPDATE_LANDING_FORM':
      return Object.assign({}, state, {
        form: {
          ...state.form,
          [action.payload.name]: action.payload.value}
      });
      break;
    case 'SUBMITTING_LANDING_FORM':
      return Object.assign({}, state, {submitting: true});
      break;
    case 'SUBMITTED_LANDING_FORM_SUCCESS':
      return Object.assign({}, state, {submitting: false});
    default:
      return state;
  }
}


