let defaultState = {
  orientation: {},
  rotation: 0
};

export default function compassReducer(state = defaultState, action) {
  switch (action.type) {
    case 'UPDATE_ROTATION':
      return Object.assign({}, state, {rotation: action.payload});
      break;
    case 'UPDATE_ORIENTATION':
      return Object.assign({}, state, {orientation:action.payload});
    default:
      return state;
  }
}





