import { combineReducers } from 'redux';
import { routerReducer as routing } from 'react-router-redux';

import compass from './compass/compass.reducer';
import landing from './landing/landing.reducer';
import loading from './loading/loading.reducer';
import firebase from './firebase/firebase.reducer';

export default combineReducers({
  routing,
  
  compass,
  landing,
  loading,
  firebase
});



