import React from 'react';
import { connect } from 'react-redux';
import Compass from './Compass';
import { getUserRef, getTrackedRef, updateLocation } from '../firebase/firebase.actions';
import { updateOrientation, updateRotation, logout } from './compass.actions';
import { getRadians, radiansToDegrees } from './compass.utils';
import * as platform from 'platform';

console.log(platform);

@connect(store => ({
  compass: store.compass,
  firebase: store.firebase
}))

export default class CompassContainer extends React.Component {
  componentWillMount() {
    this.props.dispatch(getUserRef(localStorage.getItem('email')));
    this.props.dispatch(getTrackedRef(localStorage.getItem('trackedUser')));
    this.watchOrientation();
    this.getRotation();
    this.getLocation();
  }

  orientationFunction(eventData) {
    let alpha =  eventData.webkitCompassHeading || eventData.alpha;
    this.props.dispatch(updateOrientation({
      tiltLR: eventData.gamma,
      tiltFB: eventData.beta,
      dir: alpha
    }));

    this.updateNeedle();
  }

  watchOrientation() {
    if (platform.layout === 'Blink') {
      window.addEventListener('deviceorientationabsolute', this.orientationFunction.bind(this), false);
    } else {
      window.addEventListener('deviceorientation', this.orientationFunction.bind(this), false);
    }
  }
  updateNeedle() {
    if (this.props.firebase.user.email && this.props.firebase.trackedUser.email) {
      this.getRotation();
    }
  }

  getRotation() {
    let degrees = radiansToDegrees(getRadians(this.props.firebase.user, this.props.firebase.trackedUser));
    this.props.dispatch(updateRotation(degrees));
  }

  getLocation() {
    navigator.geolocation.watchPosition(location => {
      if (this.props.firebase.user.email) {
        let coords = {
          lat: location.coords.latitude,
          lng: location.coords.longitude
        };
        this.props.dispatch(updateLocation(coords, this.props.firebase.user));
      }
    });

  }

  handleLogout() {
    this.props.dispatch(logout());
  }

  render() {
    return <Compass
      rotation={this.props.compass.rotation}
      orientation={this.props.compass.orientation.dir}
      handleLogout={this.handleLogout.bind(this)}
    />
  }


  componentWillUnmount() {
    window.removeEventListener('deviceorientation', this.orientationFunction.bind(this), false);
    window.removeEventListener('deviceorientationabsolute', this.orientationFunction.bind(this), false);
  }
};


