import {logout} from '../compass/compass.actions';
import firebase from 'firebase';

export function getUserRef(email) {
  return dispatch => {
    dispatch({type: 'FETCHING_FIREBASE_USER_REF'});

    if (!email) throw new Error('No Email Provided');

    firebase
      .database()
      .ref('users')
      .orderByChild('email')
      .startAt(email)
      .endAt(email)
      .once('value', snap => {
        let val = snap.val();
        if (val) {
          dispatch({
            type: 'FETCHED_FIREBASE_USER_REF',
            payload: {
              user: val[Object.keys(val)[0]]
            }
          });
        } else {
          dispatch(logout());
        }
      });
  };
}

export function getTrackedRef(email) {
  return dispatch => {
    dispatch({type: 'FETCHING_FIREBASE_TRACKED_REF'});

    if (!email) throw new Error('No Email Provided');

    firebase
    .database()
    .ref('users')
    .orderByChild('email')
    .startAt(email)
    .endAt(email)
    .once('value', snap => {
      let val = snap.val();
      if (val) {
        dispatch({
          type: 'FETCHED_FIREBASE_TRACKED_REF',
          payload: {
            trackedUser: val[Object.keys(val)[0]]
          }
        });
      } else {
        alert('Cannot find user with email ' + email);
        dispatch(logout());
      }
    })
  };
}

export function updateLocation(coords, user) {
  return dispatch => {
    firebase
      .database()
      .ref(`users/${user.id}`)
      .set(Object.assign({}, user, coords));
  }
}


