import React from 'react';

let Loading = props => (
  <div className="c-loading--screen" style={props.style}>

    {props.quote.split('\n').map((item, idx) => (
      <span key={idx}>{item}<br/></span>
    ))}

    <span className="c-micro c-loading__text">loading...</span>
  </div>
);

export default Loading;


