import React from 'react';
import ReactDOM from 'react-dom';

//CSS
import './style/reset.css';
import './style/style.css';

import { Provider } from 'react-redux';
import store, { history } from './src/store';

import App from './src/app/AppContainer';
import { Router, Route, IndexRoute} from 'react-router';

import CompassContainer from './src/compass/CompassContainer';
import LandingContainer from './src/landing/LandingContainer';
import LoadingContainer from './src/loading/LoadingContainer';

import * as firebase from 'firebase/app';
import 'firebase/database';

const app = document.getElementById('app');

var config = {
  apiKey: "AIzaSyBQpskH9lbtGbWhqWbobl_hsbb6qgMH5jI",
  databaseURL: "https://compass-1482013919716.firebaseio.com"
};


firebase.initializeApp(config);

ReactDOM.render(
  <Provider store={store}>
    <Router history={history}>
      <Route path="/compass/" component={App}>
        <IndexRoute component={CompassContainer} />
        <Route path="login" component={LandingContainer}/>
        <Route path="loading" component={LoadingContainer}/>
      </Route>
    </Router>
  </Provider>
  , app);



