import React from 'react';
import Landing from './Landing';
import { connect } from 'react-redux';
import { updateLandingForm, submitLandingForm } from './landing.actions';

@connect(store => ({
  landing: store.landing
}))

export default class LandingContainer extends React.Component {
  handleSubmit(event) {
    event.preventDefault();
    this.props.dispatch(submitLandingForm(this.props.landing.form));
  }

  handleUpdate(event) {
    this.props.dispatch(updateLandingForm(event.target.name, event.target.value));
  }

  render() {
    return <Landing
      handleSubmit={this.handleSubmit.bind(this)}
      handleUpdate={this.handleUpdate.bind(this)}
      form={this.props.landing.form}
    />
  }
}


