import React from 'react';
import { connect } from 'react-redux';
import Loading from './Loading';

import { cycleQuote, loadApp } from './loading.actions';

@connect(store => ({
  loading: store.loading
}))

export default class LoadingContainer extends React.Component {
  componentWillMount() {
    this.props.dispatch(cycleQuote(this.getNewQuote()));
    this.props.dispatch(loadApp());
  }

  getNewQuote() {
    let quoteIndex = Math.floor(Math.random()*this.props.loading.quotes.length);
    if (quoteIndex === this.props.loading.currentQuoteIndex) {
      return this.getNewQuote();
    } else {
      return quoteIndex;
    }
  }


  componentDidMount() {
    this.cycleQuote();
  }

  componentWillUpdate() {
    if (this.props.loading.loaded) {
      console.log('loaded');
    } else {
      console.log('not loaded');
    }
  }

  cycleQuote() {
    this.props.dispatch(cycleQuote(this.getNewQuote()));
    setTimeout(() => this.cycleQuote(), 3000);
  }

  render() {
    return <Loading id="loading-component" quote={this.props.loading.quotes[this.props.loading.currentQuoteIndex]}/>
  }
};


