import {push} from 'react-router-redux';

export function getUserLocation() {
  return dispatch => {
    dispatch({type: 'FETCHING_USER_LOCATION'});

    return new Promise((resolve, reject) => {
      navigator.geolocation.getCurrentPosition(function (location) {
        resolve(dispatch({type: 'FETCHED_USER_LOCATION', payload: {
            lat: location.coords.latitude,
            lng: location.coords.longitude
          }
        }))
      })
    });
  }
}

export function updateOrientation(orientationObj) {
  return {type:'UPDATE_ORIENTATION', payload: orientationObj};
}

export function updateRotation(degrees) {
  return {type:'UPDATE_ROTATION', payload: degrees}
}

export function logout() {
  return dispatch => {
    localStorage.removeItem('email');
    localStorage.removeItem('user');
    localStorage.removeItem('trackedUser');

    dispatch(push('/compass/login'));
  }
}



