let defaultState = {
  user: {},
  trackedUser: {}
};

export default function firebaseReducer(state = defaultState, action) {
  switch (action.type) {
    case 'FETCHING_FIREBASE_USER_REF':
    case 'FETCHING_FIREBASE_TRACKED_REF':
      return Object.assign({}, state);
      break;
    case 'FETCHED_FIREBASE_USER_REF':
    case 'FETCHED_FIREBASE_TRACKED_REF':
      return Object.assign({}, state, action.payload);
      break;
    default:
      return state
  }
}


