let defaultState = {
  quotes: [
    `A soulmate is someone who appreciates your level of weird. \n\n -Bill Murray`,
    'I kinda... sorta... maybe... well actually do... like you way more than I originally planned',
    'I want to be the reason you look down at your phone and smile. \n\n Then walk into a pole.',
    'Sometimes the smallest things take up the most room in your heart'
  ],
  currentQuoteIndex: 0,
  loading: true,
  loaded: false
};

export default function loadingReducer(state = defaultState, action) {
  switch (action.type) {
    case 'CYCLE_QUOTE':
      return Object.assign({}, state, {currentQuoteIndex: action.payload});
      break;
    case 'LOADED_APP_NO_LOGIN':
      return Object.assign({}, state, {loading: false, loaded: true});
    case 'LOADED_APP_WITH_LOGIN':
      return Object.assign({}, state, {loading: false, loaded: true});
    default:
      return state;
  }

}


