import { getUserRef } from '../firebase/firebase.actions';

export function cycleQuote(index) {
  return {type: 'CYCLE_QUOTE', payload: index};
}

export function loadApp() {
  return dispatch => {



    dispatch({type: 'LOADING_APP'});

    let email = localStorage.getItem('email');

    console.log(email);
    if(!email) {
      console.log('no email');
      return dispatch({type: 'LOADED_APP_NO_LOGIN'});
    } else {
      dispatch(getUserRef());
      return dispatch({type: 'LOADED_APP_WITH_LOGIN'});
    }


    // dispatch(getUserRef());
    // dispatch(getTrackedRef());

    // Get LocalStorage Data
    // Connect To Firebase
    // fetch TackedUser Data from firebase

  };
}



