import React from 'react';

let Compass = props => (
  <div className="compass-wrapper">
    <div className="compass-container" style={props.style}>
      <div className="compass">
        <div className="compass-needle"
          style={{
            transform: `rotate(${props.orientation + props.rotation}deg)`,
            WebkitTransform: `rotate(${props.orientation + props.rotation}deg)`
          }}>
          <div className="compass-needle__arrow"></div>
          <div className="compass-needle__butt"></div>
        </div>
      </div>
    </div>

    <div className="c-micro compass-micro" onClick={props.handleLogout}>logout</div>
  </div>
);

export default Compass;


