import { push } from 'react-router-redux';
import firebase from 'firebase';

export function updateLandingForm(name, value) {
  return {type: 'UPDATE_LANDING_FORM', payload: {name, value}};
}

export function submitLandingForm(formData) {
  return dispatch => {
    dispatch({type: 'SUBMITTING_LANDING_FORM'});

    localStorage.setItem('user', formData.name);
    localStorage.setItem('email', formData.email);
    localStorage.setItem('trackedUser', formData.other);

    firebase
      .database()
      .ref('users')
      .orderByChild('email')
      .startAt(formData.email)
      .endAt(formData.email)
      .once('value', snap => {
        let val = snap.val();
        if (val) {
          // exists, continue
          dispatch({type: 'SUBMITTED_LANDING_FORM_SUCCESS', payload:formData});
          dispatch(push('/compass/'));
        } else {
          // does not exist, create
          let id = Date.now();
          firebase
          .database()
          .ref('users/' + id)
          .set({
            id,
            name: formData.name,
            email: formData.email
          }).then(() => {
            dispatch({type: 'SUBMITTED_LANDING_FORM_SUCCESS', payload:formData});
            dispatch(push('/compass/'));
          });
        }
      });

  };
}



