import React from 'react';

let Landing = props => (
  <div style={props.style}>
    <form onSubmit={props.handleSubmit.bind(this)}>
      <label htmlFor="name">Your name</label>
      <input id="name" name="name" required type="text" onChange={props.handleUpdate} placeholder="Your name"/>

      <label htmlFor="email">Your email</label>
      <input id="email" name="email" required type="email" onChange={props.handleUpdate} placeholder="Your email"/>

      <label htmlFor="other">Loved One's email</label>
      <input id="other" name="other" required type="email" onChange={props.handleUpdate} placeholder="Loved One's Email"/>

      <button id="submit" type="submit">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50"><path d="M25 39.7l-.6-.5C11.5 28.7 8 25 8 19c0-5 4-9 9-9 4.1 0 6.4 2.3 8 4.1 1.6-1.8 3.9-4.1 8-4.1 5 0 9 4 9 9 0 6-3.5 9.7-16.4 20.2l-.6.5zM17 12c-3.9 0-7 3.1-7 7 0 5.1 3.2 8.5 15 18.1 11.8-9.6 15-13 15-18.1 0-3.9-3.1-7-7-7-3.5 0-5.4 2.1-6.9 3.8L25 17.1l-1.1-1.3C22.4 14.1 20.5 12 17 12z"/></svg>
      </button>
    </form>
  </div>
);

export default Landing;


